import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {Pdf,ServicioService}from './../../services/servicio.service';

@Component({
  selector: 'app-liquido',
  templateUrl: './liquido.page.html',
  styleUrls: ['./liquido.page.scss'],
})
export class LiquidoPage implements OnInit {
  section="";
  idea;idea2;idea3;idea4="";
  objaux:Pdf;
  pdf:Pdf={
    id :'',
    num:'',
    nombre:'',
    ambiente:'',
    factor:'',
    personaje:'',
    solitario:true,
    solido:'',
    gaseoso:'',
    liquido:'',
    P1:'',
    P2:'',
    P3_1:'',
    P3_2:'',
    P3_3:'',
    P3_4:'',
    tarjeta:'',
    tarjeta2:'',
    idea1:'',
    idea2:'',
    idea3:'',
    idea4:'',
    ideaselecionada:'',
    aprendizaje:'',
    V:'',
    C:'',
    R:'',
    P:'',
    tipo:'',
    img:''
  };
  avanzar(section){
    this.section = section;
  }
  constructor(private serv:ServicioService,
    private router:Router) { }

  async ngOnInit() {
    this.section="ideas";
    this.objaux = await JSON.parse(this.serv.getItem('_cap_pdf2'));
    this.idea=await this.objaux.idea1;
    this.idea2=await this.objaux.idea2;
    this.idea3=await this.objaux.idea3;
    this.idea4= await this.objaux.idea4;
    this.objaux.liquido="Iniciado";
     
  }
  savedData(){
    this.pdf.liquido="Terminado";
    this.serv.AddItem(this.pdf);
    this.serv.editar2(this.pdf.id,this.pdf,this.pdf.num);
  }
  savedIdea(idea){
    this.pdf=this.objaux;
    this.pdf.ideaselecionada=idea;
  }
  alLector(){
    this.router.navigateByUrl('scanner');
  }
}
